*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  // scroll-behavior: smooth;
}

.content-box-component {
  box-sizing: content-box;
}

body {
  font-family: 'Futura PT';
  text-align: left;
  position: relative;
  height: 100%;
  margin: 0 auto;
  color: #2D2D2D;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol,
li {
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

