.card {
  width: 25%;
  position: relative;
  display: flex;
  flex-direction: column;
  cursor: pointer;

  @media (max-width: 1100px) {
    width: 33%;
  }

  @media (max-width: 850px) {
    width: 332px;
    flex-shrink: 0;
  }
}

.card__wrapper {
  display: flex;
  width: 100%;

  &--buy {
    width: calc(100% + 40px);
    justify-content: space-between;
    position: absolute;
    z-index: 100;
    top: 0;
    left: -20px;
    opacity: 0;
    transition: all .4s;

    @media (max-width: 650px) {
      width: calc(100% + 20px);
      left: -10px;
    }
  }
}

.card__discount {
  position: absolute;
  z-index: 10;
  top: 8px;
  left: 8px;
  display: block;
  width: 41px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;

  font-size: 12px;
  line-height: 24px;
  letter-spacing: 0.04em;
  font-weight: 400;

  color: #D01345;

  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

.card__fav {
  z-index: 10;
  position: absolute;
  width: 22.61px;
  height: 18.68px;
  right: 8px;
  right: 16px;
  bottom: 16.38px;
  cursor: pointer;
  transition: all .4s;
  border-radius: 40%;
  background-image: url("../img/sprite.svg");

  background-repeat: no-repeat;
  background-size: auto 100%;
  display: inline-block;
  background-position: right;

  &--on {
    background-position: left;
  }
}

.card__content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: left;
  padding-top: 13px;
  padding-bottom: 33px;
  min-height: 130px;

  transition: all .4s;

  &::before {
    opacity: 0;
    @include pseudo;
    z-index: 100;
    top: 0;
    left: -20px;
    width: 20px;
    height: 70%;
    background-color: #fff;

    @media (max-width: 650px) {
      width: 10px;
      left: -10px;
    }
  }

  &::after {
    opacity: 0;
    @include pseudo;
    z-index: 100;
    top: 0;
    right: -20px;
    width: 20px;
    height: 70%;
    background-color: #fff;

    @media (max-width: 650px) {
      width: 10px;
      right: -10px;
    }
  }
}

.card__caption {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 19px;
  line-height: 24px;
  letter-spacing: 0.04em;

  @media (max-width: 650px) {
    font-size: 16px;
  }
}

.card__price {
  display: inline-block;

  &--old {
    font-size: 12px;
    line-height: 24px;
    text-decoration-line: line-through;
    opacity: 0.7;
    margin-right: 5px;

    @media (max-width: 650px) {
      font-size: 11px;
    }
  }

  &--new {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #D01345;
    @media (max-width: 650px) {
      font-size: 14px;
    }
  }
}

.card__text {
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.04em;
  opacity: 0.6;
  margin-top: 0;
  margin-bottom: 8px;
}

.card__size-list {
  @include list-style;
  display: flex;
  gap: 14px;

  font-size: 14px;
  line-height: 20px;
  display: flex;
  align-items: center;
  letter-spacing: 0.04em;

  @media (max-width: 650px) {
    gap: 10px;
  }
}

.card__size-item {
  transition: all .4s;
  padding: 2px;
  border-radius: 2px;

  &:hover {
    background-color: #000;
    color: #fff;
  }
}

.card:hover .card__wrapper--buy {
  opacity: 1;
  top: 100px;
  padding: 0 20px;
  padding-top: 12px;
  padding-bottom: 21px;
  background-color: #fff;

  @media (max-width: 650px) {
    padding: 0 10px;
    padding-bottom: 10px;
  }
}

.card:hover .card__content {
  position: relative;

  &::before {
    opacity: 1;
  }

  &::after {
    opacity: 1;
  }
}
